import Jreact from './jreact'

function render(vnode, container) { //æ¯æ¬¡è°ƒç”¨ render æ—¶ï¼Œå…ˆæŠŠä¹‹å‰çš„æ¸…ç©º
  container.innerHTML = ''
  console.log(vnode)
  _render(vnode, container)
}

function _render(vnode, container) {
  let dom = createDomfromVnode(vnode)
  container.appendChild(dom)
}


//window.c = []
function createDomfromVnode(vnode) {
  if (typeof vnode === 'string' || typeof vnode === 'number') { //å¦‚æœæ˜¯ string æˆ–è€… nubmer éƒ½å»åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
    return document.createTextNode(vnode)
  }

  if (typeof vnode === 'object') {
    if(typeof vnode.tag === 'function') { //å½“ vnode.tag æ˜¯ä¸ªå‡½æ•°æ—¶ï¼Œå°±å»åˆ›å»ºç»„ä»¶
      let component = createComponent(vnode.tag, vnode.attrs) //ç¬¬ä¸€ä¸ªå‚æ•°æ˜¯æ„é€ å‡½æ•°åï¼Œç¬¬äºŒä¸ªå‚æ•°æ˜¯ç»„ä»¶çš„å±æ€§
      renderComponent(component)
      return component.$root
    }

    let dom = document.createElement(vnode.tag)
    setAttribute(dom, vnode.attrs)
    if (vnode.children && Array.isArray(vnode.children)) {
      vnode.children.forEach(vnodeChild => {
        _render(vnodeChild, dom) //è®°å¾—è¿™é‡Œæ˜¯ _render , è¿™é‡Œçš„é€»è¾‘æ˜¯ä¸æ¸…ç©ºçš„
      })
    }
    return dom
  }
}

//åˆ›å»ºç»„ä»¶
function createComponent(constructor, attrs) {
  let component
  if(constructor.prototype instanceof Jreact.Component) {
    component = new constructor(attrs) 
  } else {
    component = new Jreact.Component(attrs) //ä½¿ç»„ä»¶å…·æœ‰ stateï¼Œ props
    component.constructor = constructor
    component.render = function() { //å¢åŠ  render æ–¹æ³•
      return this.constructor(attrs)
    }
  }
  return component
}

//æ¸²æŸ“ç»„ä»¶
function renderComponent(component) {
  let vnode = component.render()
  let dom = createDomfromVnode(vnode)

  if(component.$root && component.$root.parentNode) {
    component.$root.parentNode.replaceChild(dom, component.$root)
  }
  component.$root = dom
}

function setAttribute(dom, attrs) {
  for (let key in attrs) {
    if (/^on/.test(key)) { //å¯¹äº‹ä»¶ç»‘å®šçš„å¤„ç†ï¼Œä»¥ on å¼€å¤´çš„ï¼Œdom[onclick] = attrs[onClick]
      dom[key.toLocaleLowerCase()] = attrs[key]
    } else if (key === 'style') { //å¯¹ style çš„å¤„ç†
      Object.assign(dom.style, attrs[key]) //æ–°å¢çš„ä¼šèµ‹å€¼åˆ° dom.style ä¸Šï¼ŒåŒåçš„å±æ€§ä¼šè¦†ç›–
    } else { //å…¶ä»–çš„ç›´æ¥ä½œä¸º dom çš„å±æ€§
      dom[key] = attrs[key]
    }
  }
}

export default {
  render,
  renderComponent
}